
@import '/dist/css/style.css';
html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%
}
body{line-height:1.6;font-family:-apple-system-font,"Helvetica Neue",sans-serif
}
*{margin:0;padding:0
}
a img{border:0
}
a{text-decoration:none;-webkit-tap-highlight-color:rgba(0,0,0,0)
}
::-webkit-input-placeholder{font-family:-apple-system-font,"Helvetica Neue",sans-serif
}
.vux-1px,.vux-1px-t,.vux-1px-b,.vux-1px-tb,.vux-1px-l,.vux-1px-r{position:relative
}
.vux-1px:before{content:" ";position:absolute;left:0;top:0;width:200%;height:1px;border:1px solid #C7C7C7;color:#C7C7C7;height:200%;-webkit-transform-origin:left top;-ms-transform-origin:left top;transform-origin:left top;-webkit-transform:scale(.5);-ms-transform:scale(.5);transform:scale(.5)
}
.vux-1px-t:before{content:" ";position:absolute;left:0;top:0;right:0;height:1px;border-top:1px solid #C7C7C7;color:#C7C7C7;-webkit-transform-origin:0 0;-ms-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleY(.5);-ms-transform:scaleY(.5);transform:scaleY(.5)
}
.vux-1px-b:after{content:" ";position:absolute;left:0;bottom:0;right:0;height:1px;border-bottom:1px solid #C7C7C7;color:#C7C7C7;-webkit-transform-origin:0 100%;-ms-transform-origin:0 100%;transform-origin:0 100%;-webkit-transform:scaleY(.5);-ms-transform:scaleY(.5);transform:scaleY(.5)
}
.vux-1px-tb:before{content:" ";position:absolute;left:0;top:0;right:0;height:1px;border-top:1px solid #C7C7C7;color:#C7C7C7;-webkit-transform-origin:0 0;-ms-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleY(.5);-ms-transform:scaleY(.5);transform:scaleY(.5)
}
.vux-1px-tb:after{content:" ";position:absolute;left:0;bottom:0;right:0;height:1px;border-bottom:1px solid #C7C7C7;color:#C7C7C7;-webkit-transform-origin:0 100%;-ms-transform-origin:0 100%;transform-origin:0 100%;-webkit-transform:scaleY(.5);-ms-transform:scaleY(.5);transform:scaleY(.5)
}
.vux-1px-l:before{content:" ";position:absolute;left:0;top:0;width:1px;bottom:0;border-left:1px solid #C7C7C7;color:#C7C7C7;-webkit-transform-origin:0 0;-ms-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleX(.5);-ms-transform:scaleX(.5);transform:scaleX(.5)
}
.vux-1px-r:after{content:" ";position:absolute;right:0;top:0;width:1px;bottom:0;border-right:1px solid #C7C7C7;color:#C7C7C7;-webkit-transform-origin:100% 0;-ms-transform-origin:100% 0;transform-origin:100% 0;-webkit-transform:scaleX(.5);-ms-transform:scaleX(.5);transform:scaleX(.5)
}
.vux-tap-active{tap-highlight-color:rgba(0,0,0,0);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.vux-tap-active:active{background-color:#ECECEC
}
body{background-color:#fbf9fe;line-height:1.2
}
.weui-dialog__title{font-size:15px !important
}
.weui-dialog__ft{font-size:15px !important
}
.weui-actionsheet__cell{font-size:15px !important
}
.vux-actionsheet-menu-default{color:#333 !important
}


