
.bind-account {
    overflow: hidden;
}
.bind-account .vux-actionsheet .weui-actionsheet__cell img {
    width: auto !important;
}
.imgmw-25 {
    max-width: 25px;
}
.imgmh-25 {
    max-height: 25px;
}
.bind-account-o .checker-item {
    border: 1px solid #ececec;
    padding: 5px 20px;
    margin-right: 5px;
    border-radius: 5px;
}
.bind-account-o .checker-item:last-child {
    margin-right: 0;
}
.bind-account-o .checker-item-selected {
    background: #288af0;
    color: #fff;
}
.bind-account-o .tag-item {
    width: 70px;
    border: 1px solid #ececec;
    padding: 5px 3px;
    margin-right: 5px;
    border-radius: 5px;
    overflow: hidden;
    text-align: center;
}
.bind-account-o .tag-item:last-child {
    margin-right: 0;
}



