}
li.task_item_common dl.task_item_brief {
    float: left;
    width: 65%;
}
dl.task_item_brief dt {
    margin-bottom: 4px;
    color: #383838;
    font-size: 15px;
}
dl.task_item_brief dd {
}
dl.task_item_brief dd p {
    float: left;
    line-height: 12px;
    color: #ababab;
    margin-right: 10px;
}
dl.task_item_brief dd p i {
    float: left;
    display: inline-block;
    margin-right: 3px;
    margin-bottom: 5px;
    background: url(/dist/images/game_image/xbox_sprit.png) 0 0 no-repeat;
    background-size: 100px 200px;
    background-position: 0px -15px;
    width: 63px;
    height: 15px;
}
dl.task_item_brief dd p i.ico_list_check_speed {
    background: url(/dist/images/game_image/xbox_sprit.png) 0 0 no-repeat;
    background-size: 100px 200px;
    background-position: 0px -15px;
    width: 63px;
    height: 15px;
}
dl.task_item_brief dd p i.ico_list_recharge {
    background: url(/dist/images/game_image/xbox_sprit.png) 0 0 no-repeat;
    background-size: 100px 200px;
    background-position: 0px -35px;
    width: 60px;
    height: 15px;
}
dl.task_item_brief dd p span {
}
dl.task_item_brief dd p b {
    color: #f9a61a;
}
p.task_state_normal {
    position: absolute;
    top: 25px;
    right: 0px;
    text-align: center;
    display: block;
    width: 75px;
    height: 25px;
    line-height: 25px;
    font-size: 13px;
    color: #fc6e51;
    border: #fc6e51 solid 1px;
    border-radius: 5px;
}
p.task_state_sellout {
    position: absolute;
    top: 8px;
    right: 50px;
    width: 60px;
    height: 60px;
    background: url(/dist/images/ico_sell_out.png) 0 0 no-repeat;
    background-size: 60px 60px;
