
html{
    font-size:  28px;
}
.recommend-friend-container{
    background: #F3F3F3;
    color: #fff;
    font-size: 12px;
}
#shouyi{
    font-size: 0.373333rem;
    background:#f9a71a;
}
#shouyi .title{
    height: 1rem;
    line-height: 1rem;
    position: relative;
    padding-left:1.0rem;
}
#shouyi .title img{
    position: absolute;
    width: 0.466667rem;
    height: 0.466667rem;
    left:0.466667rem;
    top:50%;
    transform: translatey(-50%);-ms-transform: translateY(-50%);-moz-transform: translateY(-50%);-webkit-transform: translateY(-50%);
}
#shouyi .title b{
    color:#FFE372;
}
#shouyi .shouyi-num{
    border-top: 1px solid #ffae6a;
    border-bottom: 1px solid #ffae6a;
    padding: 0.6rem 0;
    overflow: hidden;
    font-size: 0.43rem;
}
#shouyi .item{
    float: left;
    width: 50%;
    text-align: center;
}
#shouyi .item p.i{
    margin-bottom: 0.32rem;
}
#shouyi .item b{
    font-size: 0.62rem;
    font-weight: 500;
}
#shouyi .rule{
    padding: 0.4rem 0.533333rem;
    padding-bottom: 0.426667rem;
    line-height: 1.8;
}
#shouyi .rule span{
    color:#FFE372;
    font-size: 13px;
}
.inlinkArea{
    background: #fff;
    margin: 0.333333rem 0;
}
.inlinkLi{
    margin-left: 0.533333rem;
    height: 1.4rem;
    border-bottom: 0.026667rem solid #F1F1F1;
    line-height: 1.4rem;
    font-size: 0.373333rem;
    padding-right: 0.48rem;
    position: relative;
    display: block;
}
.inlinkIcon{
    position: absolute;
    width: 0.666667rem;
    height: 0.666667rem;
    top:50%;
    transform: translatey(-50%);-ms-transform: translateY(-50%);-moz-transform: translateY(-50%);-webkit-transform: translateY(-50%);
}
.inlinkliText{
    display: inline-block;
    vertical-align: top;
    color: #434343;
    font-size: 0.4rem;
    margin-left: 1.013333rem;
}
.ingotoLink{
    width: 0.23rem;
    position: absolute;
    top: 50%;
    right: 0.453333rem;
    -ms-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
}
.linkliSub{
    display: inline-block;
    vertical-align: top;
    color: #aaa;
    font-size: 0.373333rem;
    float: right;
    margin-right: 0.693333rem;
}
#dailog{
    background: #fff;
    padding: 0.666667rem 0;
}
#money{
    padding: 0.533333rem 0.346667rem 0.933333rem;
    background: #fff;
    color:#17B0F6;
    font-size: 0.293333rem;
    overflow: hidden;
}
#money .m-top,#money .m-middle,#money .m-bottom{
    overflow: hidden;
    width: 100%;
    position: relative;
}
#money .m-top span,#money .m-middle span{
    float: left;
    width: 12%;
    font-size: 0.32rem;
    color: #333;
    left: 0;
}
#money .m-top ul,#money .m-middle ul{
    float: right;
    overflow: hidden;
    width: 92%;
}
#money .m-top ul li,#money .m-bottom ul li{
    float: left;
    width: 25%;
    text-align: center;
    letter-spacing: 3px;
}
#money .m-top ul li img{
    display: inline-block;
    width:0.346667rem;
    height:0.346667rem;
}
.processs{
    margin-top: 0.56rem;
    width: 100%;
    padding-left: 8%;
    position: relative;
}
.processs .mask{
    position: absolute;
    width: 94%;
    height: 100%;
    top:0;
    left:8%;
    background: rgba(255, 255, 255, 0.6);
}
.processs .mask .runman{
    width: 0.8rem;
    position: absolute;
    top:-0.64rem;
    left:-0.3rem;
}
#money .m-middle span,#money .m-top span{
    position: absolute;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform:  translateY(-50%);
    -webkit-transform: translateY(-50%);
}
#money .m-middle ul li{
    float: left;
    width: 25%;
    position: relative;
    height: 0.96rem;
}
#money .m-middle ul li img{
    position: absolute;
    width: 1.493333rem;
    height: 0.96rem;
    left: 50%;
    -ms-transform: translateX(-50%);
    transform:  translateX(-50%);
    -webkit-transform: translateX(-50%);
}
#money .m-bottom{
    padding-left: 8%;
}
#money .m-bottom ul{
    overflow: hidden;
    width: 100%;
    font-size: 0.3rem;
    color: #ED6666;
    margin-top: 0.2rem;
    font-weight: 700;
}
#money .m-bottom ul span{
    font-size: 0.32rem;
}
footer{
    padding: 1.76rem 0.8rem 0.906667rem;
    background: #F3F3F3;
}
footer .btnn{
    width: 100%;
    padding: 0.4rem 1rem;
    background: #2DBCFE;
    color: #fff;
    font-size: 0.426667rem;
    text-align: center;
    border-radius: 0.133333rem;
    position: relative;
}
footer .btnn .person{
    width: 0.72rem;
    height: 1.52rem;
    position: absolute;
    top:-1.2rem;
    left:0.826667rem;
}
@-webkit-keyframes pulse{
0%{-webkit-transform:scaleX(1);transform:scaleX(1)
}
50%{-webkit-transform:scale3d(1.05,1.05,1.05);transform:scale3d(1.05,1.05,1.05)
}
to{-webkit-transform:scaleX(1);transform:scaleX(1)
}
}
@keyframes pulse{
0%{-webkit-transform:scaleX(1);transform:scaleX(1)
}
50%{-webkit-transform:scale3d(1.05,1.05,1.05);transform:scale3d(1.05,1.05,1.05)
}
to{-webkit-transform:scaleX(1);transform:scaleX(1)
}
}
.pulse{-webkit-animation-name:pulse;animation-name:pulse
}



