
.vue-core-image-upload {
    overflow: hidden !important;
    position: absolute !important;
    height: 100%;
    width: 100%;
    top: 0;
}
.vue-core-image-upload form {
    width: 100% !important;
    height: 100% !important;
}
.task-submit-review .img-item {
    width: 30%;
    margin-right: 5%;
    display: inline-block;
}
.task-submit-review .img-item:nth-child(3n) {
    margin-right: 0;
    float: right;
}
.task-submit-review .list-item {
    border-top: 1px solid #e2e2e2;
    border-bottom: 1px solid #e2e2e2;
}
.task-submit-review h3.task-submit-review-header {
    background-color: #fff;
    border-bottom: 1px dashed #e2e2e2;
    font-size: 15px;
    color: #2bc7b5;
    font-weight: 400;
    padding-left: 10px;
    height: 40px;
    line-height: 40px;
    margin: 0;
}
.comment_upload_btn_box {
    background: #FFF;
    width: 100%;
    padding: 10px 0;
}
.comment_upload_img {
    position: relative;
    display: inline-block;
    height: 69px;
    width: 69px;
    text-align: center;
    margin: 10px;
    float: left;
    word-break: break-all;
}
.comment_upload_img a {
}
.comment_upload_img span {
    border-radius: 5px;
    height: 69px;
    width: 69px;
    display: block;
    text-align: center;
}
.comment_upload_img span.have_bgimg {
    background: url(/dist/images/comment_upload_img1.png) no-repeat;
    background-size: 69px 69px;
    padding: 12px 0px;
    font-size: 13px;
    color: #d7d7d7;
}
.comment_upload_img span a.comment_upload_iadd {
    background: url(/dist/images/comment_upload_iadd.png) no-repeat;
    height: 15px;
    width: 15px;
    background-size: 15px 15px;
    display: block;
    margin: 0 auto;
    padding-top: 6px;
}
.comment_upload_img span img {
    height: 65px;
    width: 65px;
    border-radius: 5px;
    z-index: 5px;
    position: absolute;
    top: 0px;
    left: 0px;
}
.comment_upload_img span a.comment_upload_del {
    position: absolute;
    display: block;
    height: 19px;
    width: 19px;
    background: url(/dist/images/comment_upload_del.png) no-repeat center;
    background-size: 19px 19px;
    top: -5px;
    right: -5px;
    z-index: 200;
    padding: 5px;
}
.comment_upload_note {
    padding: 0 20px;
}
.comment_upload_note li {
    font-size: 12px;
    color: #999999;
    line-height: 26px;
}
.comment_upload_note li em {
    background: #f9a71a;
    border-radius: 5px;
    height: 5px;
    width: 5px;
    display: inline-block;
    text-align: center;
    margin-right: 5px;
    line-height: 26px;
}
.comment_info_bot {
    padding: 20px;
}
.task-submit-review .comment_upload_img span img {
    height: 100%;
    width: 100%;
    padding: 1px;
}
.comment_upload_img span:last-child {
    width: 0;
    height: 0;
}




