
.product-images img {
    max-width: 49%;
    width: auto;
    max-height: 100px;
    float: left;
    display: inline-block;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    margin-right: 2%;
}
.product-images img:last-child {
    margin-right: 0;
}
.e-commerce-task-detail .weui-form-preview__bd {
    padding: 3px;
}
.e-commerce-task-detail .task_info_top dl {
    width: 60%;
}
.task_info_btn a {
    overflow: hidden;
    word-break: keep-all;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding: 0 10px;
}
.banner-detail {
    height: 80px;
    line-height: 80px;
    font-size: 15px;
    text-align: center;
    background: #ea6969;
    color: #fff;
    margin-top: 10px;
}
.footer-btns {
    background-color: #fff;
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    border-top: 1px solid #e5e5e5;
    text-align: center;
    padding: 5px;
}



