
body {
    line-height: 1.2; /* 1.2 ,1.6*/
    font-size: 12px;
}
.index-auto-money{
    position: absolute;
    right: 5px;
    top: 20px;
}
.index-auto-money img{
    max-width:60px;
}

/* */
.index-top {
    background-color: #f9a71a;
    position: relative;
}
.index-topmain-tip {
    color: #fff;
    width: 100%;
    text-align: center;
    padding-top: 15px;
    margin-bottom: 15px;
}
.index-topmain-tip p {
    font-size: 14px;
    color: #fcdbb3;
    margin-bottom: 5px;
}
.index-topmain-tip p.index-topmain-money {
    font-size: 45px;
    color: #fff;
}
ul.index-topbar {
    color: #fff;
    padding-bottom: 15px;
    border-top: 1px solid #fab948;
    padding-top: 15px;
}
ul.index-topbar li.index-topbar-left {
    float: left;
    text-align: left;
    margin-left: 15px;
}
ul.index-topbar li.index-topbar-right {
    float: right;
    text-align: right;
    margin-right: 15px;
}
ul.index-topbar li span {
    color: #fcdbb3;
}
ul.index-topbar li p {
    font-size: 24px;
}



