
.profile .avatar{
    height: 40px;
    width:40px;
    border-radius: 50%;
    border: 2px solid #fcca76;
    background: #fcce76;
}
.profile .icon {
    height:30px;
    width:30px;
    margin-right: 10px;
}
.profile .weui-cells{
    font-size: 13px;
}
.mint-msgbox {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
    background-color: #fff;
    width: 85%;
    border-radius: 3px;
    font-size: 16px;
    -webkit-user-select: none;
    overflow: hidden;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    transition: .2s;
}
.mint-msgbox-header {
    padding: 15px 0 0;
}
.mint-msgbox-content {
    padding: 10px 20px 15px;
    border-bottom: 1px solid #ddd;
    min-height: 36px;
    position: relative;
}
.mint-msgbox-input {
    padding-top: 15px;
}
.mint-msgbox-input input {
    border: 1px solid #dedede;
    border-radius: 5px;
    padding: 4px 5px;
    width: 100%;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    height:30px;
}
.mint-msgbox-input input.invalid {
    border-color: #ff4949;
}
.mint-msgbox-input input.invalid:focus {
    border-color: #ff4949;
}
.mint-msgbox-errormsg {
    color: red;
    font-size: 12px;
    min-height: 18px;
    margin-top: 2px;
}
.mint-msgbox-title {
    text-align: center;
    padding-left: 0;
    margin-bottom: 0;
    font-size: 16px;
    font-weight: 700;
    color: #333;
}
.mint-msgbox-message {
    color: #999;
    margin: 0;
    text-align: center;
    line-height: 36px;
}
.mint-msgbox-btns {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    height: 40px;
    line-height: 40px;
}
.mint-msgbox-btn {
    line-height: 35px;
    display: block;
    background-color: #fff;
    -ms-flex: 1;
    -webkit-flex: 1;
            flex: 1;
    margin: 0;
    border: 0;
}
.mint-msgbox-btn:focus {
    outline: none;
}
.mint-msgbox-btn:active {
    background-color: #fff;
}
.mint-msgbox-cancel {
    width: 50%;
    border-right: 1px solid #ddd;
}
.mint-msgbox-cancel:active {
    color: #000;
}
.mint-msgbox-confirm {
    color: #26a2ff;
    width: 50%;
}
.mint-msgbox-confirm:active {
    color: #26a2ff;
}
.msgbox-bounce-enter {
    opacity: 0;
    -webkit-transform: translate3d(-50%, -50%, 0) scale(0.7);
    transform: translate3d(-50%, -50%, 0) scale(0.7);
}
.msgbox-bounce-leave-active {
    opacity: 0;
    -webkit-transform: translate3d(-50%, -50%, 0) scale(0.9);
    transform: translate3d(-50%, -50%, 0) scale(0.9);
}
.v-modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: .5;
    background: #000;
}



